.basket__container h1 {
    background: rgba(255, 255, 255, 0.555);
    border: 1px solid #d3d3d3;
    border-radius: 25px;
    color: #6cb8b8;    
    font-size: 4rem;
    letter-spacing: 1.3rem;
    margin: 6vh auto auto 20vw;
    padding: 1rem 3rem;
    text-align: center;
    text-transform: uppercase;
    width: 22rem;
}

.basket__container__header {
    background: rgba(255, 255, 255, 0.555);
    border: 1px solid #d3d3d3;
    border-bottom: none;
    border-radius: 25px 25px 0 0;
    color: #6cb8b8;
    display: flex;
    font-size: 2rem;
    font-weight: bold;
    letter-spacing: .7rem;
    margin: 4vh auto 0;
    padding: 1.5rem 3rem 1rem;
    width: 60%;
}

    .basket__container__header1 {
        width: 58%;
    }

        .basket__container__header1 span {
            padding-left: 4rem;
        }

        .td1 {
            width: 58%;
        }

    .basket__container__header2 {
        text-align: center;
        width: 26%;
    }

        .td2 {
            width: 26%;
        }

    .basket__container__header3 {
        text-align: center;
        width: 15%;
    }

        .td3 {
            width: 15%;
        }

.basket__container__table {
    background: rgba(255, 255, 255, 0.555);
    border: 1px solid #d3d3d3;
    border-top: none;
    border-radius: 0 0 0 25px;
    font-size: 1.3rem;
    margin: 0 auto;
    max-height: 45vh;
    overflow: auto;
    padding: 0rem 3rem 1rem;
    width: 60%;
}

    .basket__container__table table {
        margin: 0 auto;
        width: 100%;
    }

    .basket__container__table th {
        color: #6cb8b8;
        font-size: 2rem;
        letter-spacing: 0.5rem;
        padding: 1rem;    
    }    
    
    .basket__delete__product {
        color: #6cb8b8;
        cursor: pointer;
        margin-right: 2rem;
    }

    .basket__container__table td {
        padding: 1rem;
    }

    .basket__container__table input {
        text-align: center;
        width: 3rem;
    }

    .basket__container__table tfoot {
        color: #6cb8b8;
        font-size: 1.6rem;
        font-weight: bold;
    }

    .table__center__content {
        text-align: center;
    }

    .basket__product__quantity__button {
        color: #6cb8b8;
        cursor: pointer;
        font-weight: bold;
        font-size: 1.3rem;
        margin: 0 1.3rem;
    }

.basket__container__summary {
    margin: 0 auto;
    padding: 0 0rem 0 6.2rem;
    width: 60%;
    display: flex;
    justify-content: flex-end;
}

    .basket__container__summary span {
        padding-right: 2rem;
    }

    .basket__container__summary p {
        background: rgba(255, 255, 255, 0.555);
        border: 1px solid #d3d3d3;
        border-radius: 0 0 25px 25px;
        border-top: none;
        color: #6cb8b8;
        letter-spacing: .3rem;
        font-size: 1.6rem;
        font-weight: bold;
        padding: 1rem 3rem;
        text-align: center;
        width: 30%;

    }

.basket__container__button {
    margin: 5vh 0;
    text-align: center;
    width: 100%;
}

    .basket__container__button span {
        background: #66c7c7;
        border-radius: 10px;
        color: white;
        cursor: pointer;
        font-size: 1.7rem;
        font-weight: bold;
        letter-spacing: 0.2rem;
        padding: 1rem 2rem;
    }

    .basket__container__button span:hover {
        background: #6cb8b8;
    }

