* {
    margin: 0;
    padding: 0;
}

.Home__container {
    background-image: url("/img/background.jpg");
    background-size: cover;
    cursor: default;
    height: 100%;
    margin: 0;
    text-align: center;
    width: 100%;
}

.Home__container__header {
    color: #0cadad;
    letter-spacing: 1rem;
    padding-top: 10vh;
}

.Home__container__header h1 {
    font-family: 'Courgette', cursive;
    font-size: 15rem;
    text-shadow: 0 0 10px rgb(255, 255, 255);
}

.Home__container__header p {
    font-family: 'Arial';
    font-size: 3rem;
    letter-spacing: 2rem;
    text-transform: uppercase;
}

.Home__container__info {
    display: -webkit-flex;
    display: flex;
    margin: 3% 5% 5% 5%;
}

.Home__container__info__panel {
    border: 1px solid lightgrey;
    width: 100%;
    margin: 3%;
    padding: 3%;
    background: rgba(255, 255, 255, 0.555);
    border-radius: 25px;
}

.Home__container__info__panel p {
    font-size: 2.2rem;
    font-family: Arial;
    letter-spacing: 0.5rem;
    margin-top: 1rem;
    color: #0cadad;
}
h3, h4 {
    font-weight: normal;
}

h2 {
    color: #008a8a;
}

.user__container {
    background-size: cover;
    background-position: center;
    display: -webkit-flex;
    display: flex;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-size: 1.5rem;
    height: 100%;
    width: 100%;
}

.user__container__left, .user__container__right {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-basis: 50%;
            flex-basis: 50%;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.user__container__header {
    background: #9dcece;
    color: white;
    border-radius: 25px;
    font-size: 2rem;
    letter-spacing: 0.7rem;
    margin: 0 7%;
    padding: 2%;
    text-align: center;
}

.user__container__left__top {
    background: rgba(255, 255, 255, 0.555);
    border: 1px solid lightgrey;
    border-radius: 25px;
    display: -webkit-flex;
    display: flex;
    margin: 2% 7%;
    padding: 3%;
}

        .user__avatar {
            border-radius: 50%;
            box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.384);
            height: 150px;
            margin: auto;
            width: 150px;
        }

        .user__container__left__top__userdata {
            margin-left: 2rem;
            width: 60%;
        }

                .user__container__left__top__userdata * {
                    line-height: 180%;
                } 

                .user__container__left__top__userdata__contact {
                    display: -webkit-flex;
                    display: flex;
                }

                .user__container__left__top__userdata__contact * {
                    padding-right: 0.6rem;
                }

                .user__container__left__top__userdata input { 
                    width: 80%;
                }

                .edit__buttons__container {
                    display: -webkit-flex;
                    display: flex;
                }

                .edit__button {
                    background: #0cadad69;
                    border-radius: 5px;
                    color: white;
                    cursor: pointer;
                    font-size: 1.5rem;
                    padding: 0.3rem 1.2rem;
                    text-shadow: 0px 0px 5px rgba(0, 0, 0, 0.384);
                    vertical-align: middle;
                }

                .edit__button:hover{
                    background: #0cadad;
                }

                .save__button span, .delete__button span {
                    background: #0cadad69;
                    border-radius: 5px;
                    color: white;
                    cursor: pointer;
                    font-size: 1.5rem;
                    padding: 0.3rem 1.2rem;
                    text-shadow: 0px 0px 5px rgba(0, 0, 0, 0.384);
                    vertical-align: middle;
                    } 
                    .delete__button span { background: #ff000069 
                }

                .unvisible {
                    display: none;
                }

.user__container__left__bottom {
    margin-top: 5%;
}

        .user__container__left__market {
            background: rgba(255, 255, 255, 0.555);
            border: 1px solid lightgrey;
            border-radius: 25px;
            display: -webkit-flex;
            display: flex;
            margin: 2% 7%;
            padding: 3%;
        }

        .user__container__left__market img {
            border-radius: 25px;
            height: 100%;
            width: 50%;
        }

        .user__container__left__market__marketdata {
            line-height: 150%;
            margin-left: 3rem;
        }

        .user__container__left__market__marketdata h3 {
            color: #008a8a;
            font-weight: bold;
        }

.user__container__right {}

        .markets__list__container {
            border: 1px solid lightgrey;
            border-radius: 25px 0px 0px 25px;
            border-right: none;
            font-family: "Roboto", "Helvetica", "Arial", sans-serif;
            font-size: 1.4rem;
            height: 72vh;
            line-height: 150%;
            margin-top: 5%;
            overflow: auto;
        }
.market__container {
    margin-top: 6vh;
}

.market__container__filter {
    display: -webkit-flex;
    display: flex;
    height: 100%;
    -webkit-justify-content: center;
            justify-content: center;
}

.market__container__filter h1 {
    background: rgba(255, 255, 255, 0.555);
    border: 1px solid lightgrey;
    border-radius: 25px;
    color: #6cb8b8;    
    font-size: 4rem;
    letter-spacing: 1.3rem;
    margin-right: 5rem;
    padding: 1rem 3rem;
    text-transform: uppercase;
}

.market__container__filter input {
    border: 1px solid #9dcece;
    border-radius: 15px 0 0 15px;
    margin-top: 1.2rem;
    padding: 1.5rem;
}

.market__container__filter select {
    background: #66c7c7;
    border: none;
    border-radius: 0 15px 15px 0 ;
    color: white;
    font-weight: bold;
    margin-left: 0.5rem;
    padding: 1.5rem;
}

.market__product__container {
    background: rgba(255, 255, 255, 0.555);
    border: 1px solid lightgrey;
    border-right: none;
    border-radius: 25px 0 0 25px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin: 5vh auto;
    max-height: 70vh;
    overflow: auto;
    width: 60vw;
}

.market__product {
    background: white;
    border: 1px solid lightgrey;
    border-radius: 25px;
    height: 220px;
    margin: 15px;
    text-align: center;
    width: 170px;
}

.market__product img {
    height: 100px;
    padding: 10px;
    width: 100px;
}

.market__product p {
    font-size: 1.3rem;
    margin: 0 1.2rem;
}

.market__product svg {
    color: #0cadad;
    cursor: pointer;
    padding-bottom: 1rem;
}
.basket__container h1 {
    background: rgba(255, 255, 255, 0.555);
    border: 1px solid #d3d3d3;
    border-radius: 25px;
    color: #6cb8b8;    
    font-size: 4rem;
    letter-spacing: 1.3rem;
    margin: 6vh auto auto 20vw;
    padding: 1rem 3rem;
    text-align: center;
    text-transform: uppercase;
    width: 22rem;
}

.basket__container__header {
    background: rgba(255, 255, 255, 0.555);
    border: 1px solid #d3d3d3;
    border-bottom: none;
    border-radius: 25px 25px 0 0;
    color: #6cb8b8;
    display: -webkit-flex;
    display: flex;
    font-size: 2rem;
    font-weight: bold;
    letter-spacing: .7rem;
    margin: 4vh auto 0;
    padding: 1.5rem 3rem 1rem;
    width: 60%;
}

    .basket__container__header1 {
        width: 58%;
    }

        .basket__container__header1 span {
            padding-left: 4rem;
        }

        .td1 {
            width: 58%;
        }

    .basket__container__header2 {
        text-align: center;
        width: 26%;
    }

        .td2 {
            width: 26%;
        }

    .basket__container__header3 {
        text-align: center;
        width: 15%;
    }

        .td3 {
            width: 15%;
        }

.basket__container__table {
    background: rgba(255, 255, 255, 0.555);
    border: 1px solid #d3d3d3;
    border-top: none;
    border-radius: 0 0 0 25px;
    font-size: 1.3rem;
    margin: 0 auto;
    max-height: 45vh;
    overflow: auto;
    padding: 0rem 3rem 1rem;
    width: 60%;
}

    .basket__container__table table {
        margin: 0 auto;
        width: 100%;
    }

    .basket__container__table th {
        color: #6cb8b8;
        font-size: 2rem;
        letter-spacing: 0.5rem;
        padding: 1rem;    
    }    
    
    .basket__delete__product {
        color: #6cb8b8;
        cursor: pointer;
        margin-right: 2rem;
    }

    .basket__container__table td {
        padding: 1rem;
    }

    .basket__container__table input {
        text-align: center;
        width: 3rem;
    }

    .basket__container__table tfoot {
        color: #6cb8b8;
        font-size: 1.6rem;
        font-weight: bold;
    }

    .table__center__content {
        text-align: center;
    }

    .basket__product__quantity__button {
        color: #6cb8b8;
        cursor: pointer;
        font-weight: bold;
        font-size: 1.3rem;
        margin: 0 1.3rem;
    }

.basket__container__summary {
    margin: 0 auto;
    padding: 0 0rem 0 6.2rem;
    width: 60%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

    .basket__container__summary span {
        padding-right: 2rem;
    }

    .basket__container__summary p {
        background: rgba(255, 255, 255, 0.555);
        border: 1px solid #d3d3d3;
        border-radius: 0 0 25px 25px;
        border-top: none;
        color: #6cb8b8;
        letter-spacing: .3rem;
        font-size: 1.6rem;
        font-weight: bold;
        padding: 1rem 3rem;
        text-align: center;
        width: 30%;

    }

.basket__container__button {
    margin: 5vh 0;
    text-align: center;
    width: 100%;
}

    .basket__container__button span {
        background: #66c7c7;
        border-radius: 10px;
        color: white;
        cursor: pointer;
        font-size: 1.7rem;
        font-weight: bold;
        letter-spacing: 0.2rem;
        padding: 1rem 2rem;
    }

    .basket__container__button span:hover {
        background: #6cb8b8;
    }


html,body {
  color: #333;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  font-size: 10px;
  height: 100%;
  margin: 0;
  overflow: hidden;
  padding: 0;
  width: 100%;
}

select {
  cursor: pointer;
}

